'use client';

// The exported code uses Tailwind CSS. Install Tailwind CSS in your dev environment to ensure all styles work.

import React, { useState } from 'react';
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription } from "@/components/ui/alert";

const App: React.FC = () => {
  const [formData, setFormData] = useState({
    groupId: '',
    inviteCode: ''
  });
  
  const [errors, setErrors] = useState({
    groupId: '',
    inviteCode: ''
  });
  
  const [submitted, setSubmitted] = useState(false);
  
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // 清除错误信息
    if (value.trim()) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };
  
  const handleSubmit = () => {
    const newErrors = {
      groupId: formData.groupId.trim() ? '' : '课题组ID不能为空',
      inviteCode: formData.inviteCode.trim() ? '' : '邀请码不能为空'
    };
    
    setErrors(newErrors);
    setSubmitted(true);
    
    if (!newErrors.groupId && !newErrors.inviteCode) {
      // 处理提交逻辑
      console.log('表单提交成功', formData);
      // 这里可以添加API调用或其他操作
    }
  };
  
  const handleReset = () => {
    setFormData({
      groupId: '',
      inviteCode: ''
    });
    setErrors({
      groupId: '',
      inviteCode: ''
    });
    setSubmitted(false);
  };
  
  return (
    <div className="min-h-screen w-full flex items-center justify-center bg-white py-12 px-4">
      <div className="w-full max-w-md">
        <Card className="p-8 shadow-md border border-gray-100">
          <div className="space-y-6">
            <div className="text-center">
              <h1 className="text-2xl font-semibold text-gray-900">加入课题组</h1>
              <p className="mt-2 text-sm text-gray-500">请输入课题组ID和邀请码完成加入</p>
            </div>
            
            <div className="space-y-4">
              <div className="space-y-2">
                <div className="flex items-center">
                  <Label htmlFor="groupId" className="text-sm font-medium text-gray-700">
                    课题组ID
                  </Label>
                  <span className="text-red-500 ml-1">*</span>
                </div>
                <Input
                  id="groupId"
                  name="groupId"
                  value={formData.groupId}
                  onChange={handleChange}
                  placeholder="请输入课题组ID"
                  className={`h-10 focus:ring-2 focus:ring-black focus:border-transparent ${
                    errors.groupId && submitted ? 'border-red-500 focus:ring-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.groupId && submitted && (
                  <p className="text-sm text-red-500 mt-1">{errors.groupId}</p>
                )}
              </div>
              
              <div className="space-y-2">
                <div className="flex items-center">
                  <Label htmlFor="inviteCode" className="text-sm font-medium text-gray-700">
                    邀请码
                  </Label>
                  <span className="text-red-500 ml-1">*</span>
                </div>
                <Input
                  id="inviteCode"
                  name="inviteCode"
                  value={formData.inviteCode}
                  onChange={handleChange}
                  placeholder="请输入邀请码"
                  className={`h-10 focus:ring-2 focus:ring-black focus:border-transparent ${
                    errors.inviteCode && submitted ? 'border-red-500 focus:ring-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.inviteCode && submitted && (
                  <p className="text-sm text-red-500 mt-1">{errors.inviteCode}</p>
                )}
              </div>
            </div>
            
            {submitted && !errors.groupId && !errors.inviteCode && (
              <Alert className="bg-green-50 text-green-800 border border-green-200">
                <AlertDescription>
                  提交成功！正在处理您的请求...
                </AlertDescription>
              </Alert>
            )}
            
            <div className="flex space-x-4 pt-2">
              <Button 
                onClick={handleSubmit}
                className="flex-1 bg-black hover:bg-gray-800 text-white !rounded-button whitespace-nowrap cursor-pointer"
              >
                加入
              </Button>
              <Button 
                onClick={handleReset}
                variant="outline" 
                className="flex-1 border-black text-black hover:bg-gray-100 !rounded-button whitespace-nowrap cursor-pointer"
              >
                返回
              </Button>
            </div>
          </div>
        </Card>
      </div>
    </div>
  );
};

export default App;
